export function RenderMatch({u, isAuthed, update}: {u: EnhancedMatch, isAuthed: number, update: () => void}) {
	const isLoggedIn = useContext(LoginContext);
	const setAlert = useContext(AlertContext);

	let {prop, opp, colour, round, id, result} = u;
	let swap = colour === 'B' && prop.tournament.setting.gamePointTotal === 1;
	let w = swap ? opp : prop;
	let b = swap ? prop : opp;
	let type = prop.tournament.setting.competitors === 'individual' ? 'player' : 'team';
	let link = Object.values(result[2]).find(l => l);
	let match = new Match({ w: w as any, b: b as any, id, tournamentId: prop.tournamentId, round, scores: [w.histories[round]?.game, b.histories[round]?.game], teams: [], link, ...result[3]}, round);
	const source = prop.tournament.setting.source ? (defaultSettings.automation.source.convert as ((v: string) => string))(prop.tournament.setting.source) : '';
	
	const handleAdd = useCallback(() => {
		setAlert({
			title: 'Add game link',
			message: `Input your ${type} game link${source ? ' from ' + source : ''}:`, 
			icon: prop.tournament.setting.source === 'chessCom' ? 'chess.com' : undefined,
			type: 'prompt',
			resolve: async (link: string) => {
				let W = colour === 'B' ? opp : prop;
				let B = colour === 'B' ? prop : opp;
				let key = await bcrypt.hash([W.id, B.id, match.tournamentId, round].join('.'), saltRounds);
				POST({
					url: '/submit/gameLink',
					data: {
						idW: W.id,
						idB: B.id,
						tournamentId: match.tournamentId,
						round,
						key,
						link
					}
				}).then(update)
			},
			prompt: {
				validate: (v: string) => prop.tournament.setting.source === 'chessCom' ? 
					type === 'team' ?
						regexes.chessCom.test(v) :
						true :
					true,
				errorMessage: type === 'team' ? 'Link must be a valid from ' + source : ''
			}
		})
	}, [setAlert, prop.tournament.setting, type, match, round, update]);
	
	return <section className={['container',userStyles.upcomingBattle].join(' ')}>
		<div className={[userStyles.stage, swap ? userStyles.reversed : ' '].join(' ')}>
			<Link to={`/${type}/${w.id}`} className={userStyles.name} style={{gridArea: 'wName'}}>
				{w.name}
			</Link>
			{w.nationality ? <div className={userStyles.flag} style={{gridArea: 'wFlag'}}>
				<img src={flags.replace('|', w.nationality)} alt={w.nationality}/>						
			</div> : null}
			<Link to={['', prop.tournament.setting.vanityURL || prop.tournamentId, 'pairings', round].join('/')} className={[userStyles.vs, userStyles.round].join(' ')}>
				{round}
			</Link>
			<div className={userStyles.vs}>
				VS
			</div>
			{b.nationality ? <div className={userStyles.flag} style={{gridArea: 'bFlag'}}>
				<img src={flags.replace('|', b.nationality)} alt={b.nationality}/>						
			</div> : null}
			<Link to={`/${type}/${b.id}`} className={userStyles.name} style={{gridArea: 'bName'}}>
				{b.name}
			</Link>
			<div className={userStyles.infoBox}>
				{prop.histories && prop.histories[round] && match.scores.some((s: number | undefined) => s) ?
					<>
						<div>Result:</div>
						<div style={{fontWeight: 700}}>{(() => {
							switch (prop.histories[round].match) {
								case 1:										
									return 'Win';
								case 0.5:										
									return 'Draw'
								case 0:										
									return 'Loss'
							}
						})()}</div>
					</> :
					null
				}
				<div>Scores:</div>
				<div>{match.scores.join('-')}</div>
				<div>{match.startTime && new Date(match.startTime).valueOf() < Date.now() ? 'Started:' : 'Starting:'}</div>				
				{match.startTime ? new Date(match.startTime).toString().slice(0, 10) + ' ' + new Date(match.startTime).toString().slice(16, 21):
					(isAuthed && false ? //TODO
						<div className={userStyles.clickButton}>Add starting time</div> :
						<div className={[userStyles.null, userStyles.clickButton].join(' ')}>No starting time added</div>
					)
				}
			</div>
			<div className={userStyles.buttons}>
			{match.scores.some((s: number | undefined) => s) ?
				<Link to={['', prop.tournament.setting.vanityURL || prop.tournamentId, 'pairings', round].join('/') + '#' + id} className={['header-button', 'link', userStyles.join].join(' ')}>
					<FaChessBoard />
					Scores
				</Link> :
				match.link ?
					<>
						<a href={match.link as string} rel='noopener noreferrer' target='_blank' className={['header-button', 'link', userStyles.join].join(' ')}>
							<FaLink />
							Join game on {source}
						</a>
						{isAuthed >= 2 ?
							<div className={['header-button', 'link', userStyles.remove].join(' ')}>
								Remove link
							</div> :
							null
						}
					</> :
					isAuthed ?
						<div className={['header-button', 'link', userStyles.join, userStyles.gameLink].join(' ')} onClick={handleAdd}>
							<FaPlusCircle />
							Add game link
						</div> :
						<div className={['header-button', userStyles.join, userStyles.null].join(' ')}>
							No game link added
						</div> 
				}						
				<Link to={['', prop.tournament.setting.vanityURL || prop.tournamentId].join('/')} className={['header-button', 'link'].join(' ')}>
					<FaChess />{prop.tournament.name}
				</Link>
				<div className={['header-button', 'link', userStyles.null].join(' ')}>
					<FaBell />Set reminder
				</div>
			</div>
		</div>
	</section>
}