import React, { useEffect, useState, useCallback, ReactElement } from 'react';
import { StaticContext } from 'react-router';
import { Route, Switch, RouteComponentProps } from 'react-router-dom';

import { GET } from '../utils/requests';
import { Team as TeamInterface, ProfileData } from '../interfaces';
import NotFound from '../404';
import Info from './Info';
import Tournaments from './Tournaments';
import { Members } from './Teams';
import { useMemoWithLabel } from '../utils/hooks';
import Play from './Play';
import { TeamSettings } from './Settings';

export default function Team(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
	profile: ProfileData
	type: 'player' | 'team'
}): ReactElement | null {

	const [player, setPlayer] = useState(undefined as TeamInterface & ProfileData | null | undefined);
	const updateProfile = useCallback(() => GET({ url: '/' + props.type + '/' + props.id })
		.then(p => {setPlayer(p); return p})
		.then(p => !p.teamId ? null : window.history.pushState('', document.title, window.location.pathname.replace(props.id, p.teamId) + window.location.search))
		.catch(() => setPlayer(null)), [setPlayer, props.id, props.type]);
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	const isAuthed: number | boolean = useMemoWithLabel(() => {
		return props.type === 'player' ?
			props.id.toLowerCase() === props.profile.uniqueID :
			typeof player?.permissions === 'number' ? player.permissions + 1 : 0;
	}, [props.type, props.id, props.profile, player], 'isAuthed');
	
	if (player === undefined) return null;
	if (player === null) return <NotFound />;
	
	return <>
		<div id='main' className={'animated-' + props.location.state?.direction}>
			<Info
				{...props}
				user={props.profile}
				profile={player}
				updateProfile={updateProfile}
				isAuthed={isAuthed as number}
				type={props.type}
			/>
			<Route render={({location}) => {
				return (
					<Switch location={location}>
						<Route exact path={`/${props.type}/${props.id}`} render={() => {
							return <Play {...props} profile={player} direction={props.location.state.direction.slice(6)} isAuthed={isAuthed as number} type={props.type} />;
						}}/>
						<Route exact path={`/${props.type}/${props.id}/tournaments`} render={() => {
							return <Tournaments {...props} user={props.profile} profile={player} isAuthed={!!isAuthed} direction={props.location.state.direction.slice(6)} type={props.type}/>	
						}}/>
						<Route exact path={`/${props.type}/${props.id}/members`} render={() => {
							return <Members {...props} team={player} isAuthed={!!player.permissions} updateProfile={updateProfile} />	
						}}/>
						<Route exact path={`/team/${props.id}/settings`} render={() => {
							if (!isAuthed) return null;
							return <TeamSettings {...props} user={props.profile} profile={player} updateProfile={updateProfile} direction={props.location.state.direction.slice(6)} />	
						}}/>
					</Switch>
				)
			}} />
		</div>
	</>
}