import React, { ReactElement, useEffect, useState, useCallback } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps } from 'react-router-dom';
import { ProfileData, Team, EnhancedPlayer, Player, PairingObject, EnhancedMatch } from '../interfaces';
import { GET } from '../utils/requests';
import SectionBreak from '../components/SectionBreak';
import { RenderMatch } from './hooks';

export default function Play(props: RouteComponentProps<any, StaticContext, any> & {
	direction: string
	profile: Team & ProfileData
	type: 'team' | 'player'
	isAuthed: number
}): ReactElement {

	const [upcoming, setUpcoming] = useState([] as EnhancedMatch[]);
	const [past, setPast] = useState([] as EnhancedMatch[]);
	const populateGames = useCallback(async () => {
		let u = [] as EnhancedMatch[];
		let p = [] as EnhancedMatch[];

		const tournaments = props.profile.tournaments.filter(t => t.active);

		let opponents = {} as {[key: string]: Player};
		let results = {} as {[key: string]: PairingObject}
		for (let t of tournaments) {
			let opps = [] as string[];
			let res = [] as string[];
			if (t.histories) for (let i = 1; i < t.histories.length; i++) {
				let h = t.histories[i];
				if (!h) continue;
				opps.push(h.id);
				let ids = [t.id, h.id];
				if (h.colour === 'B') ids = ids.reverse();
				res.push([...ids, t.tournamentId, i].join('.'));
			};

			let players = await GET({
				url: ['tournament', t.tournamentId, 'fetchPlayers'].join('/'), 
				params: { ids: opps.join(',') }
			}).catch((e) => {console.error(e); return []});
			for (let p of players) opponents[p.id] = p;

			let games = await GET({
				url: ['tournament', t.tournamentId, 'getPairings'].join('/'), 
				params: { ids: res.join(',') }
			}).catch((e) => {console.error(e); return []});
			for (let i = 0; i < games.length; i++) {
				results[res[i].split('.').slice(-2).join('.')] = games[i];
			}
		}

		for (let t of tournaments) {
			if (!t.histories) continue;
			for (let i = t.histories.length - 1; i >= 0; i--) {
				let currGame = t.histories[i];
				if (!currGame) continue;
				let match = {
					prop: t as EnhancedPlayer,
					opp: opponents[currGame.id] as Player,
					b: currGame.colour === 'B' ? t : t as any as EnhancedPlayer,
					id: currGame.colour === 'B' ? [currGame.id, t.id].join('.') : [t.id, currGame.id].join('.'),
					round: i,
					colour: currGame.colour,
					result: results[[t.tournamentId, i].join('.')]
				}
				if (currGame.match === null) u.push(match);
				else p.push(match);
			}
		}
		setUpcoming(u);
		setPast(p);
	}, [props.profile, setUpcoming, setPast])
	useEffect(() => {
		populateGames();
	}, [populateGames]);

	return <>
		<SectionBreak name='UPCOMING MATCHES'/>
		{upcoming.map(u => <RenderMatch u={u} isAuthed={props.isAuthed} update={populateGames} />)}
		<SectionBreak name='PAST MATCHES' /*expanded={!upcoming.length}*/ />
		{past.map(u => <RenderMatch u={u} isAuthed={props.isAuthed} update={populateGames} />)}
		<section />
	</>
}