import { capitalise } from '../utils/prototype';
import { TournamentStatus, Player } from '../interfaces';
import { resultsServer } from '../utils/requests';
import { TieBreaks } from './tiebreaks';

export function booleanConvert(v: boolean): 'On' | 'Off' | '' {
	if (v) return 'On';
	if (v === false) return 'Off';
	return '';
}

export interface TournamentSettings {

	tournamentId: string; // Note that the `null assertion` `!` is required in strict mode.
	pairingSystem: 'swiss' | 'round-robin';
	competitors: string
	totalRounds: number | 'Infinity';
	performanceSystem: 'ELO' | 'FIDE';
	ratingSystem: 'ECF' | 'FIDE' | 'USCF';

	displayPoints: 'match' | 'game';
	gamePointTotal: number;
	lossReward: number;
	drawReward: number;
	winReward: number;
	byeReward: 'win' | 'draw' | 'loss';
	byeGameReward: number;

	public: boolean
	timeControl: string
	location: string
	vanityURL: string
	logoURL: string
	profileURL: string
	rulesURL: string
	streamURL: string
	showRatings: boolean

	allowRegistration: 'closed' | 'approve' | 'open';
	allowNewPlayers: boolean;
	checkForDuplicatePlayers: boolean
    allowSelfLinks: number;
    allowSelfSubmissions: boolean;
    allowClashes: boolean;
	roundTimes: boolean;
	allowBonuses: boolean;

	description: string;
	tieBreaks: string;
	prizes: string;

	source?: 'lichess' | 'chessCom';
	pullResults: number;

	createdAt: string
	updatedAt: string

}

export interface TournamentSetting {
	name?: string
	enum?: string[] | number[]
	convert?: (v: any) => string
	validate?: (v: any) => boolean
	value?: (status: any, players: Player[], setting: TournamentSettings) => any
	type?: string
	locked?: boolean,
	lockWhen?: {
		[key in keyof TournamentSettings]?: any
	},
	onInit?: boolean,
	lockOnInit?: boolean
	show?: {
        [key in keyof TournamentSettings]?: any
	}
	hidden?: boolean
	allow?: {
		infinity?: boolean
		any?: boolean
		submit?: boolean
        increment?: boolean
        clear?: boolean
	}
	nb?: {
		min?: number
		max?: number
		step?: number
	}
	highlight?: boolean
	action?: string
	primaryKey?: boolean
	defaultValue?: any
	level?: number
	linked?: keyof TournamentSettings
}

export const defaultSettings = {
	core: {
		tournamentId: {
			name: 'Unique ID',
			type: 'string',
			locked: true,
			primaryKey: true
		},
		status: {
			name: 'Status',
			enum: ['uninitialised', 'active', 'finished'],
			convert: capitalise,
			defaultValue: 'uninitialised',
			value: (status: TournamentStatus): string => {
				if (!status.round) return 'uninitialised';
				if (status.active === false) return 'finished';
				else return 'active';
			}
		},
		pairingSystem: {
			name: 'Pairing system',
			enum: ['swiss', 'round-robin'],
			convert: capitalise,
			lockOnInit: true,
			defaultValue: 'swiss'
		},
		competitors: {
			name: 'Tournament type',
			enum: ['individual', 'team'],
			convert: capitalise,
			lockOnInit: true,
			defaultValue: 'individual'
		},
		totalRounds: {
			name: 'Total rounds',
			convert: (v: number | 'Infinity') => v === 'Infinity' ? '∞' : v.toString(),
			type: 'number',
			defaultValue: Infinity,
			lockWhen: {
				pairingSystem: 'round-robin'
			},
			nb: {
				min: 1,
				max: 100
			},
			allow: {
				infinity: true
			}
		},
		ratingSystem: {
			name: 'Rating system',
			enum: ['ECF', 'FIDE', 'USCF'],
			lockOnInit: true,
			defaultValue: 'ECF'
		},
		displayPoints: {
			name: 'Games per pairing',
			convert: (v: 'match' | 'game') => v === 'match' ? 'Just One' : 'Multiple',
			enum: ['match', 'game'],
			defaultValue: 'match'
		},
		gamePointTotal: {
			name: 'Number of games per pairing',
			type: 'number',
			defaultValue: 1,
			show: {
				displayPoints: 'game'
			},
			lockOnInit: true,
			nb: {
				step: 1,
				min: 1
			},
			allow: {
				any: true
			},
			highlight: true,
			linked: 'byeGameReward'
		},
		public: {
			name: 'Public',
			convert: booleanConvert,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: false,
			level: 1
		},
		tieBreaks: {
			name: 'Tie Breaks',
			convert: () => '',
			type: 'string',
			defaultValue: null,
			validate: (v: string) => {
				let list = v.split(',');
				for (let k of list) {
					if (!(k in TieBreaks)) throw `Invalid tiebreak '${k}'`;
				}
				return true;
			},
			hidden: true
		}
	},
	permissions: {        
		allowNewPlayers: {
			name: 'Mid-way joining?',
			convert: booleanConvert,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true,
			hidden: true
		},
		checkForDuplicatePlayers: {
			name: 'Check for duplicate players?',
			convert: booleanConvert,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true,
			show: {
				allowNewPlayers: true,
				pairingSystem: 'swiss'
			},
			highlight: true
		},
		allowClashes: {
			name: 'Forbid certain pairings?',
			convert: booleanConvert,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true,
			show: {
				pairingSystem: 'swiss'
			}
		},
		allowBonuses: {
			name: 'Input score bonuses for players?',
			convert: booleanConvert,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true
		}
	},
	points: {
		lossReward: {
			name: 'Points on loss',
			lockOnInit: true,
			type: 'number',
			defaultValue: 0,
			nb: {
				step: 0.1
			}
		},
		drawReward: {
			name: 'Points on draw',
			lockOnInit: true,
			type: 'number',
			defaultValue: 0.5,
			nb: {
				step: 0.1
			}
		},
		winReward: {
			name: 'Points on win',
			lockOnInit: true,
			type: 'number',
			defaultValue: 1,
			nb: {
				step: 0.1
			}
		},
		byeReward: {
			name: 'Bye reward',
			enum: ['win', 'draw', 'loss'],
			lockOnInit: true,
			defaultValue: 'win'
		},
		byeGameReward: {
			name: 'Bye reward in game points',
			type: 'number',
			lockOnInit: false,
			defaultValue: 1,
			show: {
				displayPoints: 'game'
			},
			nb: {
				min: 0
			},
			highlight: true
		},
		performanceSystem: {
			name: 'Performance rating',
			enum: ['ELO', 'FIDE'],
			lockOnInit: false,
			defaultValue: 'ELO'
		}
	},
	stats: {
		nbPlayers: {
			name: 'Number of players',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.length,
			convert: (v: number) => v.toString()
		},
		activePlayers: {
			name: 'Active players',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.filter(p => p.active).length,
			convert: (v: number) => v.toString()
		},
		matchesPlayed: {
			name: 'Matches played',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.reduce((acc, curr) => acc += curr.played, 0) / 2,
			convert: (v: number) => v.toString()
		},
		gamesPlayed: {
			name: 'Games played',
			type: 'number',
			defaultValue: 0,
			value: (status: TournamentStatus, players: Player[]): number => players.reduce((acc, curr) => acc += curr.histories.reduce((acc, curr) => curr ? acc += curr.played : acc, 0), 0) / 2,
			convert: (v: number) => v.toString()
		},
		averageRating: {
			name: 'Average rating',
			type: 'number',
			defaultValue: 1200,
			value: (status: TournamentStatus, players: Player[]): number => {
				let sum = players.reduce((acc, curr) => acc += curr.rating, 0);
				return Math.round(sum / players.length);
			},
			convert: (v: number) => v.toString()
		},
		createdAt: {
			name: 'Created at',
			convert: (v: string) => new Date(v).toString().slice(0, 24),
			locked: true
		},
		updatedAt: {
			name: 'Last updated',
			convert: (v: string) => new Date(v).toString().slice(0, 24),
			locked: true
		}
	},
	public: {
		vanityURL: {
			name: 'Custom ID',
			type: 'string',
			defaultValue: '',
			action: 'updateVanityURL',
			level: 2
		},
		url: {
			name: 'URL',
			type: 'link',
			defaultValue: '',
			value: (status: TournamentStatus, p: Player[], s: TournamentSettings): string => `${resultsServer}tournament/${s.vanityURL || status.id}`.trim(),
			level: 2
		},
		timeControl: {
			name: 'Time control',
			type: 'string',
			defaultValue: '',
			level: 2
		},
		location: {
			name: 'Location',
			type: 'string',
			defaultValue: '',
			level: 2
		},
		profileURL: {
			name: 'Link to website',
			type: 'string',
			level: 2
		},
		rulesURL: {
			name: 'Link to rules',
			type: 'string',
			level: 2
		},
		logoURL: {
			name: 'Link to profile picture',
			type: 'string',
			level: 2
		},
		streamURL: {
			name: 'Stream link',
			type: 'string',
			level: 2
		}
	},
	public_permissions: {
		showRatings: {
			name: 'Show ratings',
			convert: booleanConvert,
			type: 'boolean',
			defaultValue: true,
			level: 1
		},
		roundTimes: {
			name: 'Show round times',
			convert: booleanConvert,
			type: 'boolean',
			defaultValue: true,
			locked: false,
			level: 1
		}
	},
	public_hidden: {
		description: {
			name: 'Description',
			type: 'string',
			level: 1
		},
		prizes: {
			name: 'Prizes',
			type: 'string',
			level: 1
		}
	},
	automation: {
		source: {
			name: 'Source',
			enum: ['', 'lichess', 'chessCom'],
			convert: (v: string): string => {
				if (v === 'lichess') return 'Lichess.org';
				if (v === 'chessCom') return 'Chess.com';
				return 'None';
			},
			defaultValue: '',
			level: 1
		},
		pullResults: {
			name: 'Pull results',
			show: {
				source: ['lichess', 'chessCom']
			},
			enum: [0, 1, 2],
			type: 'number',
			highlight: true,
			level: 1,
			convert: (v: number) => {
				if (v === 0) return 'Off';
				if (v === 1) return 'Ask me first';
				return 'Smart Pull and save all';
			},
			defaultValue: 1
		},
		allowSelfLinks: {
			name: 'Let users submit links?',
			lockOnInit: false,
			enum: [0, 1, 2, 3],
			type: 'number',
			defaultValue: 1,
			highlight: true,
			level: 1,
			convert: (v: number) => {
				if (v === 0) return 'Off';
				if (v === 1) return 'Ask me first';
				if (v === 2) return 'Save if both players confirm';
				return 'Smart if either player confirms';
			}
		},
		allowSelfSubmissions: {
			name: 'Let users submit results?',
			convert: booleanConvert,
			locked: true,
			lockOnInit: false,
			type: 'boolean',
			defaultValue: true
		},
		allowRegistration: {
			name: 'Let users register online?',
			convert: capitalise,
			enum: ['closed', 'approve', 'open'],
			onInit: false,
			defaultValue: 'closed'
		}
	}
} as {
	[key: string]: {
		[key: string]: TournamentSetting
	}
};